import React, { useState } from 'react';
import { UserSessionState } from '../types';
import { generateQueryWorkbook, generateGridWorkbook, handleExcelToggle } from '../utils/exportUtils';

interface ExportButtonsProps {
  onReset: () => void;
  state: UserSessionState;
  showNotification: (message: string, type?: 'info' | 'success' | 'error' | 'warning') => void;
}

export const ExportButtons: React.FC<ExportButtonsProps> = ({
  onReset,
  state,
  showNotification
}) => {
  const [isExportingDownload, setIsExportingDownload] = useState(false);
  const [isExportingExcelWeb, setIsExportingExcelWeb] = useState(false);
  const [isResetting, setIsResetting] = useState(false);

  const handleExportDownload = async () => {
    try {
      setIsExportingDownload(true);
      console.log('Exporting data for download...');
      showNotification('Preparing download export...', 'info');
      
      // Get the current active tab from session manager
      const activeTab = state.activeTab;
      console.log('Active tab for export:', activeTab);
      
      let blob: Blob | null = null;
      
      console.log('Generating workbook for active tab:', activeTab);
      if (activeTab === 'pq-query') {
        // For PQQuery tab: call generateQueryWorkbook to get the blob
        console.log('Generating workbook for PQQuery tab...');
        blob = await generateQueryWorkbook(state);
      } else if (activeTab === 'grid') {
        // For Grid tab: call generateGridWorkbook to get the blob
        console.log('Generating workbook for Grid tab...');
        blob = await generateGridWorkbook(state);
      } else {
        // For other tabs
        showNotification(`Export not available for ${activeTab} tab`, 'warning');
        console.log(`Export not available for tab: ${activeTab}`);
        return;
      }
      
      if (blob) {
        // Call handleExcelToggle with download mode (true)
        console.log('Calling handleExcelToggle for download...');
        await handleExcelToggle(blob, true, activeTab);
        
        showNotification('Workbook downloaded successfully', 'success');
      } else {
        throw new Error('Failed to generate workbook blob');
      }
      
    } catch (error) {
      console.error('Error exporting data for download:', error);
      const message = error instanceof Error ? error.message : 'Unknown error';
      showNotification(`Download export failed: ${message}`, 'error');
    } finally {
      setIsExportingDownload(false);
    }
  };

  const handleExportExcelWeb = async () => {
    try {
      setIsExportingExcelWeb(true);
      console.log('Exporting data for Excel Web...');
      showNotification('Preparing Excel Web export...', 'info');
      
      // Get the current active tab from session manager
      const activeTab = state.activeTab;
      console.log('Active tab for export:', activeTab);
      
      let blob: Blob | null = null;
      
      console.log('Generating workbook for active tab:', activeTab);
      if (activeTab === 'pq-query') {
        // For PQQuery tab: call generateQueryWorkbook to get the blob
        console.log('Generating workbook for PQQuery tab...');
        blob = await generateQueryWorkbook(state);
      } else if (activeTab === 'grid') {
        // For Grid tab: call generateGridWorkbook to get the blob
        console.log('Generating workbook for Grid tab...');
        blob = await generateGridWorkbook(state);
      } else {
        // For other tabs
        showNotification(`Export not available for ${activeTab} tab`, 'warning');
        console.log(`Export not available for tab: ${activeTab}`);
        return;
      }
      
      if (blob) {
        // Call handleExcelToggle with Excel Web mode (false)
        console.log('Calling handleExcelToggle for Excel Web...');
        await handleExcelToggle(blob, false, activeTab);
        
        showNotification('Opened in Excel Web successfully', 'success');
      } else {
        throw new Error('Failed to generate workbook blob');
      }
      
    } catch (error) {
      console.error('Error exporting data for Excel Web:', error);
      const message = error instanceof Error ? error.message : 'Unknown error';
      showNotification(`Excel Web export failed: ${message}`, 'error');
    } finally {
      setIsExportingExcelWeb(false);
    }
  };

  const handleResetClick = async () => {
    // Show confirmation dialog
    const confirmed = window.confirm(
      'Reset your workspace? This will clear all your data.\n\n' +
      'This action cannot be undone. Your session will be reset to its default state.'
    );
    
    if (!confirmed) {
      return;
    }
    
    try {
      setIsResetting(true);
      showNotification('Resetting workspace...', 'info');
      
      // Call the parent reset function
      await onReset();
      
      showNotification('Workspace reset successfully', 'success');
      
      // Reload the page to show clean state
      setTimeout(() => {
        window.location.reload();
      }, 1000);
    } catch (error) {
      console.error('Error during reset:', error);
      showNotification('An error occurred while resetting. Please try again.', 'error');
    } finally {
      setIsResetting(false);
    }
  };

  return (
    <div className="excel-toggle-container">
      <div className="export-buttons">
        <button
          id="exportDownloadButton"
          className="export-button download-button"
          type="button"
          onClick={handleExportDownload}
          disabled={isExportingDownload || isExportingExcelWeb}
        >
          <span className="export-icon">📥</span>
          {isExportingDownload ? 'Downloading...' : 'Export Download'}
        </button>
        <button
          id="exportExcelWebButton"
          className="export-button excel-web-button"
          type="button"
          onClick={handleExportExcelWeb}
          disabled={isExportingDownload || isExportingExcelWeb}
        >
          <span className="export-icon">🌐</span>
          {isExportingExcelWeb ? 'Opening...' : 'Export to Excel Web'}
        </button>
      </div>
      <button
        id="resetButton"
        className="reset-button"
        type="button"
        onClick={handleResetClick}
        disabled={isResetting}
      >
        <span className="reset-icon">{isResetting ? '⏳' : '⚠️'}</span>
        {isResetting ? 'Resetting...' : 'Reset'}
      </button>
    </div>
  );
};

export default ExportButtons;